import React from 'react';
import {StyledBasketForm, StyledFormButton, StyledFormHeading} from "./styled";
import FormControl from "./FormControl";
import {Input} from "../../../controls/input/Input";
import {Select} from "../../../controls/select/Select";
import {Textarea} from "../../../controls/textarea/Textarea";
import AgreementSection from "./AgreementSection";

const BasketForm = (): JSX.Element => {
    const confirmMock = (e: React.SyntheticEvent) => {
        e.preventDefault();
        alert("   !");
    }
    return (
        <StyledBasketForm>
            <StyledFormHeading> </StyledFormHeading>
            <FormControl label="">
                <Input placeholder="  "/>
            </FormControl>
            <FormControl label=" ">
                <Input placeholder="+7 (000) 000-00-00"/>
            </FormControl>
            <FormControl label="E-mail">
                <Input placeholder="example@mail.ru"/>
            </FormControl>
            <FormControl label="  ">
                <Select>
                    <option></option>
                    <option></option>
                    <option selected={true} disabled={true}>   </option>
                </Select>
            </FormControl>
            <FormControl label="">
                <Textarea rows={5} placeholder="   "/>
            </FormControl>
            <AgreementSection/>
            <StyledFormButton appearance="long"
                              onClick={confirmMock}></StyledFormButton>
        </StyledBasketForm>
    );
};

export default BasketForm;
