import React from 'react';
import {StyledNavbar} from "./styled";
import {Button} from "../../../controls/button/Button";
import AnchorLink from "react-anchor-link-smooth-scroll";

const Navbar = (): JSX.Element => (
    <StyledNavbar>
        <AnchorLink href='#company'><Button appearance='big'> </Button></AnchorLink>
        <AnchorLink href='#payment'><Button appearance='big'></Button></AnchorLink>
        <AnchorLink href='#delivery'><Button appearance='big'></Button></AnchorLink>
        <AnchorLink href='#sale'><Button appearance='big'></Button></AnchorLink>
        <AnchorLink href='#catalog'><Button appearance='big'></Button></AnchorLink>
        <AnchorLink href='#contacts'><Button appearance='big'></Button></AnchorLink>
        <AnchorLink href='#reviews'><Button appearance='big'></Button></AnchorLink>
    </StyledNavbar>
);

export default Navbar;
