import React from 'react';
import {
    DuoContainer, DuoContainer2,
    FooterInfo, FooterPNG, FooterSVG,
    PicturesContainer,
    StyledFooter,
    StyledFooterLink,
    StyledFooterText,
} from "./styled";
import visa from "../../images/pay_methods/visa.png";
import mastercard from "../../images/pay_methods/MasterCard.png";
import mir from "../../images/pay_methods/Mir.png";
import phone from "../../images/svg/phone.svg";
import {format} from 'date-fns';

const Footer = (): JSX.Element => (
    <StyledFooter>
        <DuoContainer>
            <StyledFooterText> :</StyledFooterText>
            <PicturesContainer>
                <FooterPNG src={visa} alt="visa image"></FooterPNG>
                <FooterPNG src={mastercard} alt="mastercard image"></FooterPNG>
                <FooterPNG src={mir} alt="mir image"></FooterPNG>
            </PicturesContainer>
        </DuoContainer>
        <DuoContainer2>
            <StyledFooterLink>  <br/>  </StyledFooterLink>
            <StyledFooterLink>  <br/> </StyledFooterLink>
        </DuoContainer2>
        <FooterInfo>
            <StyledFooterText>
                2017 - {format(new Date(), 'yyyy')}  VAZ Paradise -     
            </StyledFooterText>
        </FooterInfo>
        <DuoContainer className="disappear">
            <StyledFooterText> , . 88 <br/> +7(902)999-99-99</StyledFooterText>
            <StyledFooterText> , . 77 <br/> +7(952)999-99-99</StyledFooterText>
        </DuoContainer>
        <FooterSVG src={phone}/>
    </StyledFooter>
);

export default Footer;
